import { ReactNode } from "react";

/**
 * tr,td 
 * @param values 
 * @returns {@link ReactNode}
 */
export const trInTable = (values: string[]): ReactNode => {
  let tds: ReactNode[] = [];

  values.forEach((val: string, index: number) => {
    tds.push(<td className="solid" key={index}>{val}</td>);
  });

  return <tr>{tds}</tr>;
};

// overload 
// Java  C# overload 
// typescript 

export const tdsInTr = (vals: ({ val: string, col?: number })[]): ReactNode => {

  let tds: ReactNode[] = [];
  vals.forEach((data: ({ val: string, col?: number }), index: number) => {
    tds.push(
      <td className="solid" key={index} colSpan={data.col !== undefined ? data.col : 1}>
        {data.val}
      </td>);
  });

  return <tr>{tds}</tr>;
}
