import { trInTable, tdsInTr } from "../../ts/utils";

/** Ryzen8000G  */
const Ryzen8000 = () => {
  return <>
    <div className="margin10">  Ryzen 8000G  </div>

    {/* thead,tbody DEV */}
    <table className="collapse">
      <thead>
        <CpuModel />
      </thead>
      <tbody>
        <Clock />
        <CoreAndThread />
        <L2Cache />
        <L3Cache />
        <GpuName />
        <GpuCore />
        <GpuClock />
      </tbody>
    </table>

    <div className="margin-top20">  </div>
    <ul className="margin-top10">
      <li> DDR5-5200 </li>
      <li> TDP65W </li>
      <li>AM5 </li>
    </ul>

    <div className="margin-top20">  </div>
    <ul className="margin-top10">
      <li>  <br /> </li>
      <li>
        DDR5-6000
        "5200"
      </li>
      <li> AMDCore i5 13400F + GTX1650  </li>
    </ul>
  </>
};

const GpuClock = () => <>{
  tdsInTr([
    { val: "GPU" },
    { val: "2900MHz" },
    { val: "2800MHz", col: 2 },
    { val: "2600MHz" },
  ])
}</>;

/** GPU */
const GpuName = () => <>{
  tdsInTr([
    { val: "GPU" },
    { val: "Radeon 780M" },
    { val: "Radeon 760M" },
    { val: "Radeon 740M", col: 2 },
  ])
}</>;

/** GPU */
const GpuCore = () => <>{
  tdsInTr([
    { val: "GPU" },
    { val: "12" },
    { val: "8" },
    { val: "4", col: 2 },
  ])
}</>;

/** UPU  */
const CpuModel = () => <>{
  //<>{}</> is not a valid JSX element

  trInTable(["CPU", "7 8700G", "5 8600G", "5 8500G", "3 8300G"])
}</>;

/** / */
const CoreAndThread = () => <>{
  tdsInTr([
    { val: "/" },
    { val: "8/16" },
    { val: "6/12", col: 2 },
    { val: "4/8" }
  ])
}</>;

const Clock = () => <>{
  trInTable(["", "4.2GHz", "4.3GHz", "3.5GHz", "3.4GHz"])
}</>;

const L2Cache = () => <>{
  tdsInTr([
    { val: "L2 " },
    { val: "8MB" },
    { val: "6MB", col: 2 },
    { val: "4MB" }
  ])
}</>;

const L3Cache = () => <>{
  tdsInTr([
    { val: "L3 " },
    { val: "16MB", col: 3 },
    { val: "8MB" }
  ])
}</>;

export default Ryzen8000;
