import React from "react";
import { AgGridReact } from "ag-grid-react";
import {
    ColDef, ClientSideRowModelModule, ModuleRegistry, TextEditorModule,
    NumberEditorModule, ValidationModule, DateEditorModule, RowSelectionModule,
    SelectEditorModule, RowStyleModule,
} from "ag-grid-community";

/**  */
export const DataSheet = () => {
    const [rowData] = React.useState(getData()); // 
    const [selectedIds, setSelectedIds] = React.useState<number[]>([])
    const [newValueList, setNewValueList] = React.useState<NewValues<TableData>[]>([]);

    // 
    ModuleRegistry.registerModules([
        ClientSideRowModelModule,
        NumberEditorModule, // number  Module
        TextEditorModule, // string  Module
        DateEditorModule, // Date 
        RowSelectionModule, // 
        SelectEditorModule, // select 
        RowStyleModule, // 
        ValidationModule, // Module 
    ]);

    return (
        <>
            <div style={{ height: 500 }}>
                <AgGridReact<TableData>
                    columnDefs={columns}
                    rowData={rowData}
                    getRowStyle={(e) => ({
                        // 
                        background: newValueList.find(x => x.id === e.data?.id)
                            ? "#ffefd5"
                            : "white",
                    })}
                    onCellValueChanged={(e) => {
                        // 
                        const entity = {
                            field: e.colDef.field,
                            newValue: e.newValue,
                            id: e.data.id,
                        }
                        setNewValueList(pre => [...pre, entity])
                    }}
                    gridOptions={{ rowSelection: { mode: 'multiRow' } }} // 
                    onSelectionChanged={(e) => {
                        //  ID 
                        setSelectedIds(e.api.getSelectedRows().map(x => x.id))
                    }}
                />
                <div className="m-2 px-4 py-1 rounded w-fit bg-sky-100">
                    {selectedIds.join(',')}
                </div>
                <div className="m-2 px-3 rounded w-fit bg-sky-100">
                    {newValueList.map((x, index) => (
                        <div key={index} className="flex">
                            <div className="w-20">ID{x.id}</div>
                            <div className="w-[200px]">{x.field}</div>
                            <div className="pr-2">{String(x.newValue ?? '')}</div>
                        </div>
                    ))}
                </div>
            </div>
        </>
    );
}

const LanguageValues = ['English', 'Spanish', 'French', 'Portuguese', '(other)']

type TableData = {
    id: number;
    language: string;
    a: string;
    b: number;
    createAt: Date
}

/**  */
const columns: ColDef<TableData>[] = [{
    // 
    headerName: "ID",
    field: "id",
    width: 80,
}, {
    headerName: "",
    field: "language",
    cellEditor: 'agSelectCellEditor',
    cellEditorParams: {
        values: LanguageValues,
    },
    editable: true, width: 150,
}, {
    headerName: "A",
    field: "a",
    editable: true, width: 100,
}, {
    headerName: "B",
    field: "b",
    editable: true, width: 100,
}, {
    headerName: "",
    field: "createAt",
    width: 150,
    editable: true,
}, {
    headerName: "",
    field: "a",
    cellRenderer: () => (
        <button
            className="rounded my-1 px-2 bg-gray-200"
            type="button" onClick={() => alert('click')}
        >
            button
        </button>
    ), width: 100,
}];

/**  */
const getData = () => {
    // 
    return [60, 45, 20, 50, 30].map((val, index) => (
        {
            id: index + 1,
            language: LanguageValues[index % 4],
            a: `test-${val}`,
            b: val * 1.2,
            createAt: new Date(`2024/12/${index + 1}`)
        }
    ));
}

/**  */
type NewValues<Data> = {
    id: number;
    field?: keyof Data;
    newValue?: string | number | Date;
}